import React, {useContext} from "react";
import {Fade} from "@stahl.luke/react-reveal";
import emoji from "react-easy-emoji";
import "./Greeting.scss";
import landingPerson from "../../assets/lottie/landingPerson";
import DisplayLottie from "../../components/displayLottie/DisplayLottie";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";

import {illustration, greeting} from "../../portfolio";
import StyleContext from "../../contexts/StyleContext";

import Skills from "./Skills";

export default function Greeting() {
  const {isDark} = useContext(StyleContext);
  if (!greeting.displayGreeting) {
    return null;
  }
  return (
    <Fade bottom duration={1000} distance="40px">
      <div className="greet-main" id="greeting">
        <div className="greeting-main">
          <div className="greeting-text-div">
            <div>
              <h1 className={isDark ? "dark-mode greeting-text" : "greeting-text"}>
                {" "}
                {greeting.title.text} <span className="mainColor">{greeting.title.name}</span> {" "}
                <span className="wave-emoji">{emoji("👋")}</span>
              </h1>
              <p className={ isDark ? "dark-mode greeting-text-p" : "greeting-text-p subTitle" } >
                {greeting.subTitle1}
                <span className="mainColor">{greeting.role}</span>
                {greeting.subTitle2}
                <span className="mainColor">{greeting.techs}</span>
                {greeting.subTitle3}
              </p>
              <SocialMedia />
              <div className="button-greeting-div">
                { greeting.displayContactMe && <Button text="Contact me" href="#contact" /> }
                {greeting.resumeLink && (
                  <Button text="See my resume" newTab={true} href={greeting.resumeLink} />
                )}
              </div>
            </div>
            <Skills />
          </div>
          <div className="greeting-image-div">
            {illustration.animated ? (
              <DisplayLottie animationData={landingPerson} /*style={{ height: '550px'}}*/ />
            ) : <img alt="man sitting on table" src={require("../../assets/images/manOnTable.svg")} />
            }
          </div>
        </div>
      </div>
    </Fade>
  );
}
